import React from 'react';
import { connect } from 'react-redux';
import { OrderStatus } from '../enums/order';
import { startCloseOrder } from '../actions/order';
import axios from 'axios';

const PizzaTypes = [
    'None', 
    'Thick crust',
    'Thin crust',
    'Whole wheat flour'
];
const ToppingTypes = [
    'None',
    'Olives',
    'Cheese',
    'Mushrooms',
    'Anchovy',
    'Tuna',
    'Corn',
    'Souce',
    'Hot pepper'
];
const StatusTypes = [
    'Done',
    'Prepearing',
    'Baking',
    'Boxing',
    'Delievering'
];


class OrdersListItem extends React.Component{
    constructor(props){
        super(props);

        this.interval = null;

        this.state = {
            Id: props.Id, 
            CustomerId: props.CustomerId, 
            ToppingType: props.ToppingType, 
            PizzaType: props.PizzaType, 
            Status: props.Status
        };
    }

    checkOrderStatus = () => {
        axios({
            method: 'get',
            url: `http://localhost:8080/api/orders/${this.state.Id}`,
            data: order
          }).then((response) => {
            this.setState(() => ({
                Status: parseInt(response.Status)
            }));
          });
    };

    onComponentDidMount(){
        this.interval = window.setInterval(60000);
    };

    onComponentWillUnmount(){
        window.clearInterval(this.interval);
        this.interval = null;
    };

    handleCloseOrder = (e) => {
        startCloseOrder(this.state.Id);
        this.props.history.push('/');
    };

    render(){
        return (
            <section>
                <p>Type: {PizzaTypes[this.state.PizzaType]}</p>
                <p>Topping: {ToppingType[this.state.ToppingType]}</p>
                <p>Current status: {StatusTypes[this.state.Status]}</p>
                {
                    this.state.Status === OrderStatus.DELIEVERING 
                    && 
                    <button
                        onClick={this.handleCloseOrder}
                    >
                        I've got the pizza
                    </button>
                }
            </section>
        );
    };
}

const mapDispatchToProps = (dispatch) => ({
    startCloseOrder: (id) => dispatch(startCloseOrder(id))
});
export default connect(undefined,mapDispatchToProps)(OrdersListItem);


